import React, { useContext } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { allLanguages } from 'config/localisation/languageCodes'
import { LanguageContext } from 'contexts/Localisation/languageContext'
import useTheme from 'hooks/useTheme'
import { usePriceCakeBusd } from 'state/hooks'
import { Menu as UikitMenu } from '@robustswap-libs/uikit'
import config from './config'
import configtw from './configtw'
import configcn from './configcn'
import { getCakeAddress } from '../../utils/addressHelpers'

const Menu = (props) => {
  const { connect, reset } = useWallet()
  const { account } = useWallet()
  const { selectedLanguage, setSelectedLanguage } = useContext(LanguageContext)
  const { isDark, toggleTheme } = useTheme()
  const cakePriceUsd = usePriceCakeBusd()
  let links = config;
  if (selectedLanguage.code === 'zh-CN') {
    links = configcn;
  }
  else if (selectedLanguage.code === 'zh-TW') {
    links = configtw;
  }

  return (
    <UikitMenu
      account={account}
      login={connect}
      logout={reset}
      isDark={isDark}
      toggleTheme={toggleTheme}
      currentLang={selectedLanguage && selectedLanguage.code}
      langs={allLanguages}
      setLang={setSelectedLanguage}
      cakePriceUsd={cakePriceUsd.toNumber()}
      cakePriceLink={`https://bscscan.com/token/${getCakeAddress()}`}
      links={links}
      {...props}
    />
  )
}

export default Menu
